import React, { FC, memo, useCallback } from "react";
import Table from "../../components/Table/Table";
import { Project, User } from "../../typing";
import dayjs from "dayjs";
import { Link } from "react-router-dom";
import { useDeleteProject, useEditProject } from "utils/project";
import Pin from "components/pin";
import { Dropdown, Menu, Modal } from "antd";
import { ButtonNoPadding } from "components/lib";
import {
  useProjectModal,
  useProjectsQueryKey,
} from "screens/project-list/utils";

export interface ListProps {
  list: Project[];
  users: User[];
  loading: boolean;
}

const List: FC<ListProps> = memo((props) => {
  const { list, users, loading } = props;
  const { mutate } = useEditProject(useProjectsQueryKey());

  const pinProject = useCallback(
    (id: number) => (pin: boolean) => mutate({ id, pin }),
    [mutate]
  );

  return (
    <Table
      loading={loading}
      columns={[
        {
          title: <Pin checked={true} disabled={true} />,
          render(_value, project) {
            return (
              <Pin
                checked={project.pin}
                onCheckedChange={pinProject(project.id)}
              />
            );
          },
        },
        {
          title: "",
          dataIndex: "name",
          render(_value, project) {
            return <Link to={String(project.id)}>{project.name}</Link>;
          },
        },
        {
          title: "",
          dataIndex: "organization",
        },
        {
          title: "",
          render(_value, project) {
            return (
              <span>
                {users.find((user) => user.id === project.personId)?.name ||
                  ""}
              </span>
            );
          },
        },
        {
          title: "",
          render(_value, project) {
            return (
              <span>
                {project.created
                  ? dayjs(project.created).format("YYYY-MM-DD")
                  : ""}
              </span>
            );
          },
        },
        {
          render(_value, project) {
            return <More project={project} />;
          },
        },
      ]}
      dataSource={list}
    />
  );
});

const More = ({ project }: { project: Project }) => {
  const { startEdit } = useProjectModal();
  const editProject = (id: number) => () => startEdit(id);
  const { mutate: deleteProject } = useDeleteProject(useProjectsQueryKey());
  const confirmDeleteProject = (id: number) => {
    Modal.confirm({
      title: "",
      content: "",
      okText: "",
      onOk() {
        deleteProject({ id });
      },
    });
  };

  return (
    <Dropdown
      overlay={
        <Menu>
          <Menu.Item onClick={editProject(project.id)} key="eidt">
            
          </Menu.Item>
          <Menu.Item
            onClick={() => confirmDeleteProject(project.id)}
            key="delete"
          >
            
          </Menu.Item>
        </Menu>
      }
    >
      <ButtonNoPadding type="link">...</ButtonNoPadding>
    </Dropdown>
  );
};

export default List;
