import { useForm } from "antd/lib/form/Form";
import React, { useCallback, useEffect } from "react";
import { useDeleteTask, useEditTask } from "utils/task";
import { useTasksModal, useTasksQueryKey } from "./util";
import { Button, Form, Input, Modal } from "antd";
import UserSelect from "components/user-select";
import TaskTypeSelect from "components/task-type-select";

const layout = {
  labelCol: { span: 8 },
  wrapperCol: { span: 16 },
};

const TaskModal = () => {
  const [form] = useForm();
  const { editingTaskId, editingTask, close } = useTasksModal();
  const { mutateAsync: editTask, isLoading: editLoading } = useEditTask(
    useTasksQueryKey()
  );
  const { mutate: deleteTask } = useDeleteTask(useTasksQueryKey());

  const onCancel = useCallback(() => {
    close();
    form.resetFields();
  }, [close, form]);

  const onOk = useCallback(async () => {
    let fields = form.getFieldsValue();
    // {value,label}value
    fields.typeId = fields.typeId.value;
    fields.processorId = fields.processorId.value;
    const editData = { ...editingTask, ...fields };
    await editTask(editData);
    close();
  }, [editTask, close, editingTask, form]);

  const startDelete = useCallback(() => {
    close();
    Modal.confirm({
      okText: "",
      cancelText: "",
      title: "",
      onOk() {
        return deleteTask({ id: Number(editingTaskId) });
      },
    });
  }, [editingTaskId, deleteTask, close]);

  useEffect(() => {
    form.setFieldsValue(editingTask);
  }, [form, editingTask]);

  return (
    <Modal
      forceRender={true}
      onCancel={onCancel}
      onOk={onOk}
      okText=""
      cancelText=""
      confirmLoading={editLoading}
      title=""
      visible={!!editingTaskId}
    >
      <Form {...layout} initialValues={editingTask} form={form}>
        <Form.Item
          label={""}
          name="name"
          rules={[{ required: true, message: "" }]}
        >
          <Input />
        </Form.Item>
        <Form.Item label="" name="processorId">
          <UserSelect
            personId={editingTask?.processorId || ""}
            defaultOptionName=""
          />
        </Form.Item>
        <Form.Item label="" name="typeId">
          <TaskTypeSelect value={editingTask?.typeId || ""} />
        </Form.Item>
      </Form>
      <div style={{ textAlign: "right" }}>
        <Button
          onClick={startDelete}
          style={{ fontSize: "14px" }}
          size="small"
          danger
          type="link"
        >
          
        </Button>
      </div>
    </Modal>
  );
};

export default TaskModal;
